import "./globals.css"; // Import des styles globaux
import Header from "./components/Header/Header"; // Si tu veux inclure le Header

export default function Layout({ children }) {
 return (
  <div>
   <Header /> {/* Affiche le Header avec la NavBar */}
   <main>{children}</main> {/* Contenu dynamique des pages */}
  </div>
 );
}
